.post-container {
    width: 70vw ;
    background-color: #fcdddd ;
    display: flex ;
    align-items: center ;
    padding: 1.5rem 2rem ;
    border-radius: 5px ;
    margin: 1.3rem 0 ;
    border-left: 5px solid rgb(223, 62, 62) ;
    box-shadow: 0px 4px 6px 4px rgba(214, 0, 54, 0.2) ;
}

.logo {
    display: flex ;
    align-items: center ;
    padding-right: 1.5rem ;
}

.part1 {
    display: flex ;
    flex-direction: column ;
    flex-grow: 1 ;
    padding-right: 50px ;
}

.company {
    font-weight: 700 ;
    color: #FF5555 ;
    margin-right: 0.8rem ;
    display: flex ;
    flex-direction: column ;
    flex-grow: 1 ;

}

