.title-position {
    flex-direction: column ;
    flex-grow: 1 ;
    font-weight: 700 ;
    font-size: 1.1rem ;
    display: inline-block ;
    padding: 0.7rem 0 ;
}

.details {
    display: flex ;
    flex-grow: 1 ;
    color: rgb(150, 150, 150) ;
}

.part2 {
    display: flex ;
    flex-wrap: wrap ;
    justify-content: space-between ;
    padding-right: 30px;
}

.description {
    background-color: white ;
    color: #FF5555 ;
    font-weight: 700 ;
    padding: 0.5rem ;
    border-radius: 10px ;
}
.button-menu{
    cursor: pointer;
}
.dropdown-menu{
    position: absolute;
    background-color: #FF5555;
    border: 1px solid black;
    border-radius: 8px;
    padding: 10px 14px 10px 14px;
    left: 82.5%;
}
.dropdown-list{
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.dropdown-button{
    cursor: pointer;
}